@media only screen and (min-width: 512px){
#header .container {
    margin-top: 92px !important;
}
}

#header h2 {
    color: #fff;
    font-size: 2.4rem;
    font-weight: 700;
    line-height: 60px;
    /* margin-bottom: 25px; */
}

.form-control {
    width: 100%;
    height: 46px;
    padding: 0 20px;
    font-size: 14px;
    border-radius: 30px;
    border: 1px solid #ececec;
    -webkit-transition: all .3s;
    -moz-transition: all .3s;
    transition: all .3s;
}

.input-group-text {
    border-radius: 100% 0% 0% 100%;
}

.search-form .button {
    background: #26ae61;
}

.search-form .button {
    font-size: 22px;
    line-height: 13px;
    display: inline-block;
    position: relative;
    outline: none !important;
    background: #26ae61;
    color: #fff;
    cursor: pointer;
    border: none;
    text-align: center;
    overflow: hidden;
    padding: 10px 11px;
    transition: .3s;
    border-radius: 4px;
    z-index: 1;
}
