.f-category {
    transition: all .3s linear;
    border: 1px solid #f1f1f1;
    margin-top: -1px;
    background: #fff;
    padding: 20px;
    text-align: center;
    overflow: hidden;
    display: block;
    margin-right: -1px;
}

.f-category .icon {
    width: 64px;
    height: 64px;
    display: inline-block;
    border-radius: 50%;
    margin-top: 5px;
    margin-bottom: 15px;
    transform: scale(1);
    -moz-transform: scale(1);
    -webkit-transform: scale(1);
    transition: all .3s linear;
}

